import { getComments } from '@/utils/article.js'
export default {
  name: 'articleComments',
  props: {
    article: {
      required: true,
      type: Object
    }
  },
  data() {
    return {
      commentList: []
    }
  },
  async mounted() {
    // 数据不走SEO
    const { data } = await getComments({
      slug: this.article.slug
    })
    console.log('comments.data', data)
    this.commentList = data.comments
  }
}
